.Loader_wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes Loader_items-1 {
  0% {
    top: -16px;
    height: 232px;
  }
  50% {
    top: 42px;
    height: 116px;
  }
  100% {
    top: 42px;
    height: 116px;
  }
}
@keyframes Loader_items-2 {
  0% {
    top: -1.5px;
    height: 203px;
  }
  50% {
    top: 42px;
    height: 116px;
  }
  100% {
    top: 42px;
    height: 116px;
  }
}
@keyframes Loader_items-3 {
  0% {
    top: 13px;
    height: 174px;
  }
  50% {
    top: 42px;
    height: 116px;
  }
  100% {
    top: 42px;
    height: 116px;
  }
}
.Loader_items div {
  position: absolute;
  width: 28px;
}
.Loader_items div:nth-child(1) {
  left: 36px;
  background: #1976d2;
  animation: Loader_items-1 2.0833333333333335s cubic-bezier(0, 0.5, 0.5, 1)
    infinite;
  animation-delay: -0.4166666666666667s;
}
.Loader_items div:nth-child(2) {
  left: 86px;
  background: #2196f3;
  animation: Loader_items-2 2.0833333333333335s cubic-bezier(0, 0.5, 0.5, 1)
    infinite;
  animation-delay: -0.20833333333333334s;
}
.Loader_items div:nth-child(3) {
  left: 136px;
  background: #bbdefb;
  animation: Loader_items-3 2.0833333333333335s cubic-bezier(0, 0.5, 0.5, 1)
    infinite;
  animation-delay: undefineds;
}

.Loader {
  width: 200px;
  height: 200px;
  display: inline-block;
  overflow: hidden;
  background: none;
}
.Loader_items {
  width: 100%;
  height: 100%;
  position: relative;
  transform: translateZ(0) scale(1);
  backface-visibility: hidden;
  transform-origin: 0 0;
}
.Loader_items div {
  box-sizing: content-box;
}
