.Footer {
  width: 100vw;
  height: 60px;
  background: #212121;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 200px;
  color: #fff;
}
.Navbar {
  display: flex;
  justify-content: center;
  align-items: center;
}
.Navbar > a {
  margin: 0 10px;
  font-size: 25px;
  color: #fff;
  transition: 0.3s;
}
.Navbar a:nth-child(1):hover {
  color: #448aff;
}
.Navbar a:nth-child(2):hover {
  color: #7b1fa2;
}
